<script>
import TableComponent from "@/components/Table/Table.vue";
import Pagination1 from "@/components/Pagination1/Pagination1.vue";

export default {
  name: "contractEngineer-component",
  components: { TableComponent, Pagination1 },
  data() {
    return {
      workerLoading1: false,
      emp_no: "", // 原簽核的工號選單
      workerLoading2: false,
      new_emp_no: "", // 新簽核的工號選單

      contractTableColumns: [
        { id: "index", label: "項次", width: "75" },
        { id: "empInfo", label: "Emp Name(Emp No)", width: "175" },
        { id: "project_No", label: "Project No", width: "175" },
        { id: "project_Name", label: "Project Name", width: "" },
        { id: "status", label: "Status", width: "140" },
      ],
      selectedContract: [],
    };
  },
  computed: {
    workerList() {
      return this.$store.state.signoff_workerList;
    },
    // 發包工程師資料 (All)
    contract() {
      return this.$store.state.contract_all;
    },
    // 發包工程師資料 (單頁)
    contractList() {
      this.$store.state.contract_allList.forEach((e) => {
        e.status = this.convertStatus(e.project_Status);
      });
      return this.$store.state.contract_allList;
    },
  },
  methods: {
    // 按 enter 後蒐取清單，供原簽核工號選用
    async workerSearchHandler1(evt) {
      this.workerLoading1 = true;
      if (evt.key == "Enter") {
        this.workerLoading1 = false;
        if (!evt.target.value) return;
        this.$store.dispatch("getWorkerList", evt.target.value);
      }
    },
    async searchContract() {
      try {
        await this.$store.dispatch("getContract", this.emp_no);
        this.$store.commit("CURRENT_PAGE_INIT");
      } catch (err) {
        console.log(err);
      }
    },

    // 勾選多筆工程
    changeSelection(item) {
      this.$store.state.signoff_workerList = [];
      this.selectedContract = item.map((e) => e.project_No);
    },
    // 按 enter 後蒐取清單，供新簽核工號選用
    async workerSearchHandler2(evt) {
      this.workerLoading2 = true;
      if (evt.key == "Enter") {
        this.workerLoading2 = false;
        if (!evt.target.value) return;
        this.$store.dispatch("getWorkerList", evt.target.value);
      }
    },
    async changeContract() {
      try {
        await this.$store.dispatch("updContract", {
          NewEmp_No: this.new_emp_no,
          OldEmp_No: this.emp_no,
          Prjs: this.selectedContract,
        });
        this.$message({
          type: "success",
          duration: 5000,
          showClose: true,
          message: "變更成功",
        });

        // 清空所選的工程、新發包者，並更新工程資料 (呈現第一頁)
        this.selectedContract = [];
        this.new_emp_no = "";
        this.searchContract();
      } catch (err) {
        console.log(err);
      }
    },

    changePage(page) {
      this.$store.commit("CHANGE_PAGE_HANDLER", {
        page,
        paramsDataName: "contract_all",
        paramsListName: "contract_allList",
      });
    },
  },
  mounted() {
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;

    // 清空先前的查詢結果
    this.$store.state.signoff_workerList = [];
    this.$store.state.contract_all = [];
    this.$store.state.contract_allList = [];
  },
};
</script>

<template src="./contractEngineer.html"></template>
